.Header {
  background-color: #FFFFFF;
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.Header h1 {
  font-family: 'Nunito', 'sans-serif';
  font-size: 4rem;
}

.Header h1:after {
  content: '';
  width: 100%;
  position: relative;
  bottom: 12px;
  height: 15px;
  right: -2em;
  background-image: -webkit-gradient(linear, left top, right top, from(#66FFE7), to(#FDEBAC));
  background-image: -webkit-linear-gradient(left, #66FFE7, #FDEBAC);
  background-image: linear-gradient(to right, #66FFE7, #FDEBAC);
  display: block;
}

.header-content {
  display: flex;
  margin-bottom: 3em;
  height: 2em;
  align-items: center;
  background-color: white;
  border-radius: 4px;
  padding: 0 .5em;
}

.Section {
  width: 100%;
  padding-top: 2em;
}

.Section-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-flow: wrap;
}

.Section-list h1 {
  flex-basis: 100%;
  width: 100%;
  margin-top: 0;
  text-align: center;
  font-weight: normal;
  font-family: 'Nunito', 'sans-serif';
}

.Section .Section-icon {
  text-align: center;
  margin: 0 auto;
  background-color: white;
  height: 4em;
  width: 4em;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: .5em;
}

.Section .Section-icon img {
  width: 80%;
}

.Education {
  margin: 1em;
  padding: 1em;
  flex-basis: calc(25% - 2em);
  box-sizing: border-box;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}

.Education.Education--extended {
  flex-basis: calc(50% - 2em);
}

.Education:before {
  margin-top: 25%;
  display: block;
  content: '';
}

.Education .content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1em;
  box-sizing: border-box;
  z-index: 2;
  visibility: visible;
  opacity: 1;
  -webkit-transition: opacity .5s, visibility .5s;
  transition: opacity .5s, visibility .5s;
}

.Education .content,
.Education .content:hover,
.Education .content:active,
.Education .content:link,
.Education .content:visited,
.Education .content:focus {
  text-decoration: none;
}

.Education .content span {
  text-align: center;
}

.Education .content .icon {
  font-size: 250%;
  margin-bottom: .5rem;
}

.Education .details {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  top: 0;
  left: 0;
  -webkit-clip-path: circle(70% at -30% 25%);
          clip-path: circle(70% at -30% 25%);
  -webkit-transition: color .5s, -webkit-clip-path .5s;
  transition: color .5s, -webkit-clip-path .5s;
  transition: clip-path .5s, color .5s;
  transition: clip-path .5s, color .5s, -webkit-clip-path .5s;
  z-index: 1;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: transparent;
  font-size: 80%;
}

.Education.Education--extended .details {
  -webkit-clip-path: circle(45% at -20% 20%);
          clip-path: circle(45% at -20% 20%);
}

.Education .details div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: wrap;
}

.Education .details svg {
  margin-right: .3em;
}

.Education .details h4 {
  margin: .5em 0 0 0;
  text-align: center;
  flex-basis: 100%;
  font-weight: normal;
}

.Education .details span {
  margin: 0 .5em;
}

.Education:hover .content {
  visibility: hidden;
  opacity: 0;
}

.Education:hover .details {
  color: white;
  -webkit-clip-path: circle(150% at 0% 0%);
          clip-path: circle(150% at 0% 0%);
}

@media screen and (max-width: 800px) {
  .Education {
    flex-basis: calc(50% - 2em);
  }

  .Education.Education--extended {
    flex-basis: calc(100% - 2em);
  }
}

.Network {
  margin: 1em;
  padding: 1em;
  flex-basis: calc(16.6666% - 2em);
  box-sizing: border-box;
  border: 1px solid black;
  border-radius: 10px;
  position: relative;
  cursor: pointer;
  display: inline-block;
  -webkit-transition: background-color .5s, color .5s;
  transition: background-color .5s, color .5s;
}

.Network:before {
  margin-top: 60%;
  display: block;
  content: '';
}

.Network .content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1em;
  box-sizing: border-box;
}

.Network .content,
.Network .content:hover,
.Network .content:active,
.Network .content:link,
.Network .content:visited,
.Network .content:focus {
  text-decoration: none;
}

.Network .content span {
  text-align: center;
}

.Network .content .icon {
  font-size: 250%;
  margin-bottom: .5rem;
}

@media screen and (max-width: 800px) {
  .Network {
    flex-basis: calc(33.3333% - 2em);
  }
}

.App {
  width: 100%;
  height: 100%;
  display: block;
}

body, html, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  display: block;
  font-family: Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333333;
  background-color: rgb(184, 214, 226);
}

#root {
  display: flex;
}

